.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  &__sidebar {
    width: 320px; /* Ширина меню */
    flex-shrink: 0; /* Не сжимается */
    transition: transform 0.3s ease; /* Плавное появление/скрытие */
  }

  &__content {
    flex-grow: 1; /* Основной контент занимает всё доступное пространство */
    margin-left: 0; /* По умолчанию отступ слева отсутствует */
    transition: margin-left 0.3s ease; /* Плавное изменение отступа */
    width: 100%;
    overflow-x: hidden; // Скрывает горизонтальный скролл
  }
}

.services__img {
  max-width: 100%;
  img {
    width: 100%;
    height: auto;
  }
}


@media (min-width: 1440px) {
  .layout {
    flex-direction: row;

    &__sidebar {
      position: fixed; /* Фиксируем меню */
      top: 0;
      left: 0;
      width: 320px; /* Ширина меню */
      height: 100vh; /* Высота на весь экран */
      z-index: 100; /* Убедимся, что меню поверх контента */
      transform: translateX(0); /* Всегда видимо */
      background-color: #ffffff; /* Цвет фона меню */
      box-shadow: 16px 0 52px rgba(14, 24, 80, 0.2), -2px 0 4px rgba(69, 79, 126, 0.02); /* Тень */
    }

    &__content {
      margin-left: 320px; /* Отступ слева равен ширине меню */
      flex-grow: 1; /* Основной контент занимает всё доступное пространство */
      width: calc(100% - 320px);
    }

    /* На десктопе меню всегда открыто, поэтому убираем класс layout--sidebar-open */
    &--sidebar-open {
      .layout__sidebar {
        transform: translateX(0); /* Меню всегда видимо */
      }

      .layout__content {
        margin-left: 320px; /* Контент всегда сдвинут */
      }
    }
  }
}