.repair-tech {
  padding-bottom: 24px;

  &__section-title {
    margin-bottom: 16px;
  }

  &__list {
    margin-bottom: 40px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    width: 240px;
    height: 160px;
    margin-left: 16px;
    padding: 16px;
    background-color: #ffffff;
    border: 1px solid #eaeaea;
    border-radius: 6px;
  }

  &__link {
    align-self: flex-end;
  }

  &__title {
    align-self: flex-start;
  }
}

@media (min-width: $screen-md) {
  .repair-tech {
    &__section-title {
      margin-bottom: 24px;
    }

    &__list {
      display: flex;
      flex-wrap: wrap;
      max-width: 100%;
      max-height: 160px; 
      margin: 0 4px 24px;
      overflow: hidden;
      
      &--show {
        max-height: 1000px; 
        opacity: 1;
      }
    }

    &__item {
      width: 224px;
      margin: 0 12px 16px;
      opacity: 0;
      transform: translateY(10px);
      transition: opacity 1s ease;
      
      &:nth-child(-n+4) { 
        opacity: 1;
        transform: translateY(0);
      }
    }

    &__list--show &__item {
      opacity: 1;
      transform: translateY(0);
    }

    &__showmore {
      margin-left: 32px;
    }


    &__showmore {
      display: block;
    }
  }
}
